.topWrapper{
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.videoBlockWrapper{
    height: 90%;
    position: relative;
}
.videoBlockOverlayWrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 60%;
    top:0;
    left:0;
    z-index: 3;

}
.projectsBlockVideoBg{
    object-fit: cover;
    height: 100%;
    width: 100%;
    float: left;
    padding: 0;
    position: relative;
}
.projectTitle{
    position: absolute;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
    margin-top: 40px;
    margin-bottom: 40px;
    background-color: rgba(68, 70, 64, 0.3);
    font-family: 'Apexnewweb book',sans-serif;
    color: #f5f1f1;
    font-size: 62px;
    line-height: 35px;
    font-weight: 400;
    text-align: center;
    letter-spacing: 20px;
    text-transform: uppercase;
    width: 70%;
    z-index: 4;
}
.buttonsContainer{
    background-color: #8d9382;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
    gap: 10px;

}
.projectBody{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 1550px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    margin-top: 20px;

}
@media (max-width: 950px){
    .projectTitle{
        border:2px solid grey;
    }
}
    @media (max-width: 750px) {
    .buttonsContainer{
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }
}
@media (max-width: 450px){
    .projectTitle{
        font-size: 30px;
        line-height: 42px;
        letter-spacing: 5px;
        width: 95%;
        background-color: transparent;
    }
}
